.forgetpasswordcontainer {
  width: 100%;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}
.forgetpassword {
  width: 25%;
  background-color: rgb(179, 210, 255);
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 20px;
}
.input-form-box-container {
  width: 40%;
  height: auto;
  background-color: rgb(215, 215, 215);
  border-radius: 20px;
}

.inputbox-update {
  margin: 10px;
  margin-left: 6px;
  width: 87%;
  padding: 15px;
  border-radius: 10px;
  border: 1px solid rgb(195, 195, 195);
}

#button-submit {
  padding: 15px;
  width: 200px;
  background-color: blueviolet;
  color: white;
  border-radius: 10px;
  border: none;
  cursor: pointer;
}
.buttoncon {
  margin-bottom: 40px;
}
