.main-container-loader {
  width: 100%;
  height: 100vh;
  background-color: rgb(255, 255, 255);
  color: rgb(255, 0, 123);
  position: fixed;
  z-index: 1;
  top: 0px;
  
}

.loader-animation-container{
  width: 100%;
  height: 55%;
  display: flex;
  align-items: flex-end;
  justify-content: center;
}
.heading-container-loader{
  width: 100%;
  height: 80px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.loader{
  width: 50px;
  height: 50px;
  border: 7px solid rgb(127, 127, 127);
  border-radius: 50%;
  border-top: 7px solid rgb(0, 255, 68);
  animation: rotate 2s infinite;
}
@keyframes  rotate{
  0%{
    transform:rotate(0deg);
  }
  100%{
    transform: rotate(360deg);
  }
}