.allproject-details-list-container {
  width: 100%;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}
.allproject-details-box-container {
  width: 100%;
  height: 100%;
  background-color: rgb(0, 255, 191);
  overflow-x: hidden;
  overflow-y: scroll;
}
.allproject-details-scroll {
  width: 100%;
  height: auto;
}
.allproject-details-box {
  width: 260px;
  height: auto;
  float: left;
  background-color: rgb(203, 235, 255);
  margin: 20px;
  border-radius: 10px;
}
.product-image-box-container {
  width: 100%;
  height: 180px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.product-image-box {
  width: 100%;
  height: 90%;
}
.product-title-box-container,
.product-discription-box-container,
.product-price-box-container {
  width: 90%;
  height: auto;
  padding:  0px 10px;
  
}
.product-title-box,
.product-discription-box,
.product-price-box {
  padding: 5px;
  font-weight: 600;
  display: flex;
  text-align: justify;
}
.product-price-button-container {
  width: 100%;
  height: 80px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.edit-button-box {
  width: 100px;
  padding: 10px;
  border-radius: 10px;
  border: none;
  outline: none;
  background-color: rgb(238, 0, 255);
  color: white;
  margin: 20px;
  margin-top: 0px;
  margin-bottom: 0px;
  cursor: pointer;
}
