.maingetuserdata-container {
  width: 95%;
  height: 90%;
  background-color: rgb(255, 255, 255);
  border-radius: 20px;
  border: 1px solid rgb(212, 212, 212);
}
.maingetuserdata-profile-heading {
  width: 100%;
  height: 80px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-bottom: 1px solid rgb(212, 212, 212);
}
.maingetuserdata-profile-text {
  width: 100%;
  height: 60px;
  display: flex;
  align-items: center;
  border-bottom: 1px solid rgb(212, 212, 212);
}
.top-text{
    margin-top: 20px;

}
.spanclassflex {
  display: flex;
  margin-left: 20px;
  font-size: 25px;
  font-weight: 700;
  display: flex;
  align-items: center;
  justify-content: center;
  color: red;
}
.value-name {
  font-size: 20px;
  font-weight: 500;
  margin-left: 20px;
  color: rgb(0, 144, 0);
}
.userchangepassword{
    width: 100%;
    height: 100px;
    display: flex;
    align-items: center;
    justify-content: center;
    border: none;
}
.changepassword-button{
padding: 15px;
width: 200px;
border: none;
border-radius: 10px;
background-color:crimson;
color: white;
cursor: pointer;
font-weight: 600;
}